@tailwind base;
@tailwind components;
@tailwind utilities;

@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600;700;800;900&display=swap');


*{
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  scroll-behavior: smooth;
  scroll-padding-top: 46px;
}

body{
  color: white;
  font-family: 'Poppins', 'sans-serif';
  background-color: #18181B;
}

:root{
   --yellow:#eee8aa; /*#ffc107 */
}


#menu-btn, #large-btn{
  cursor: pointer;
  width: 1.3rem;
  transition: all 0.25s;
  position: relative;
  background-color: rgb(37, 37, 37, 0);
  border: none;
  outline: none;
  padding-bottom: 1rem;
}


.hamburger-top,
.hamburger-middle,
.hamburger-bottom{
  height: 1.5px;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  background-color: white;
  transform: rotate(0);
  transition: all 0.5s;
}

.hamburger-middle{
  transform: translateY(6px);
}

.hamburger-bottom{
  transform: translateY(12px);
}

.open_hamburger {
  transform: rotate(90deg);
  transform: translateY(0px);
}

.open_hamburger .hamburger-top{
  transform: rotate(45deg) translateY(6px) translate(6px);
}

.open_hamburger .hamburger-middle{
  display: none;
}

.open_hamburger .hamburger-bottom{
  transform: rotate(-45deg) translateY(6px) translate(-6px);
}

/* width */
::-webkit-scrollbar {
  width: 0px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #18181B;
}

/*
 Handle */
::-webkit-scrollbar-thumb {
  background: var(--yellow);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: var(--yellow);
} 


html{
  font-size: 16px;
}

.loading_page{
  background-color: #18181B;
  position: absolute;
  color: var(--yellow);
  height: 100vh;
  width: 100%;
  overflow: hidden;
  z-index: 20;
}

.loading_page img{
  height: 5rem;
  animation-name: myanimation;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
}


@keyframes myanimation {
  0%   {
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -o-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1)
  }
  50%  {
    -webkit-transform: scale(1.1);
    -moz-transform: scale(1.1);
    -o-transform: scale(1.1);
    -ms-transform: scale(1.1);
    transform: scale(1.1)
  }
  100%  {
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -o-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1)
  }
}

nav{
 position: fixed;
  width: 100%;
  z-index: 15;
  background-color: #18181B;
  box-shadow: 0px 0px 10px rgb(12, 12, 12);
}

.page{
  position: relative;
}


nav > i{
  position: relative;
  z-index: 5;
  cursor: pointer;
}

.content{
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 100%;
  box-shadow: 0px 0px 10px rgb(12, 12, 12);
  position: relative;
}

.main__nav{
  display: flex;
  z-index: 1;
}


#side.open{
  transform: translateX(0);
}

.side_bar{
  z-index: 2;
  position: fixed;
  min-height: 100vh;
  width: 40vh;
  max-width: 300px;
  top: 0;
  left: 0;
  transition: all ease-in-out 600ms;
  transform: translateX(-150%);
  box-shadow: 4px 0px 10px rgb(12, 12, 12);
}

.side_bar_content{
  height: 100%;
  display: grid;
  grid-template-rows: auto 1fr auto;
}

.side_bar_content > div:nth-of-type(2){
overflow: scroll;
overflow-x:hidden;
}

.side_bar_content hr{
  margin: .1rem 0;
  border: .5px solid #2b2b31;
}

.side_bar_content li::before{
  content: '\f00c';
  font-family: "Font Awesome 6 free"; 
	font-weight: 900;
  margin-right: .5rem;
  font-size: 1rem;
  color: var(--yellow);
}

.cv_btn{
  width: 100%;
  background-color: var(--yellow);
  font-weight: bold;
  color: #18181B;
  font-size: .9rem;
}

.hover_btn:hover{
transition: transform ease-in-out 200ms;
transform: scale(.96);
}

.card{
  text-align: center;
}

 .card > div{
  /* height: 7.5rem;
width: 7.5rem;
border-radius: 50%;
display: flex;
justify-content: center;
align-items: center;
border: 2px white solid; */
position: relative;
} 

.card > div span{
  position: absolute;
  height: 1rem;
  width: 1rem;
  border-radius: 50%;
  background-color: var(--yellow);
  bottom: 2%;
  right: 15%;
}

.card > div span::after{
content: ' ';
height: 1.5rem;
width: 1.5rem;
background-color: white;
}


.card > div > img{
height: 6rem;
width: 6rem;
border-radius: 50%;
}

.social{
  width: 100%;
  font-size: 1.5rem;
  transition: all ease-in 400ms;
}

.social a:hover{
  color: var(--yellow);
  transform: translateY(-.3rem);
}

#navbar{
  flex-shrink: 0;
  position: fixed;
  min-height: 100vh;
  top: 0;
  right: 0;
  transition: all ease-in-out 600ms;
  transform: translateX(150%);
  display: grid;
  grid-template-rows: auto 1fr;
  box-shadow: 0px 4px 10px rgb(12, 12, 12);
  z-index: 5;
}

.navBtn{
  box-shadow: 8px 3px 10px rgb(12, 12, 12);
}


#navbar.open{
  transform: translateX(0);
}

#navbar.left{
  width: 50%;
  left: 0;
}

#navbar_content > div{
  margin-top: 7rem;
}

#navbar_content ul{
  color: white;
}

.main{
  width: 100%;
  flex-shrink: 0;
  position: relative;
  left: 0;
  transition: left ease-in-out 600ms;
}

.main.left{
  left: 0;
}

.bg-image{
  width: 100%;
  height: 25rem;
  position: relative;
}

.bg-image > img{
  position: absolute;
  height: 100%;
  width: 100%;
}

.bg-image > div{
  position: absolute;
  height: 100%;
  width: 100%;
  background-image: linear-gradient(to bottom, rgb(0,0,0,.8) ,rgb(24, 24, 27,.9), #18181B);
}

.main > main{
  position: absolute;
  top:0;
  width: 100%;
  height: 100%;
}

 main .hero{
  background-repeat: no-repeat;
  background-size: cover;
  background-image:linear-gradient(rgba(0,0,0,0.7), rgba(0,0,0,0.7)), url(https://images.unsplash.com/photo-1610552050890-fe99536c2615?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxzZWFyY2h8NHx8bW91bnRhaW4lMjBsYW5kc2NhcGV8ZW58MHx8MHx8&w=1000&q=80);
}

.avatar{
  height: 15rem;
  -webkit-filter: drop-shadow(5px 5px 5px rgb(12, 12, 12));
  filter: drop-shadow(5px 5px 5px rgb(12, 12, 12));
  animation: slideInAvatar ease-in-out 1200ms;
}

@keyframes slideInAvatar{
  from{
    transform: translateX(150%);
  }
  to{
    transform: translateX(0);
  }
}

.hero h1{
  font-size: 2rem;
  font-weight: 800;
}

.hero a{
  padding: .8rem;
  text-align: center;
  background-color: var(--yellow);
  color: #18181B;
  font-weight: bold;
  max-width: fit-content;
}

.hero #typed_text{
font-size: 1.1rem;
}

.hero #typed_text strong{
  text-decoration: underline 3px var(--yellow);
}

.typing_effect{
  height: 2rem;
}

.type-wrap{
  display: inline;
}

.hero code{
  font-weight: bold;
}

.hero code span{
  color: var(--yellow);
}

.blockquote {
    position: relative;
    font-weight: 800;
    font-family: 'Raleway';
    color: #ffffff;
    padding: 2rem 0;
    width: 100%;
    max-width: 500px;
    z-index: 1;
    margin: 2rem auto;
    align-self: center;
    border-bottom: solid 1px;
    border-top: solid 1px;
}

/* Blockquote header */
.blockquote h1 {
    position: relative;
    color: #FFFFFF;
    font-size: 2.5rem;
    font-weight: 800;
    line-height: 1;
    margin: 0;
}

/* Blockquote right double quotes */
.blockquote:after {
    position: absolute;
    content: "\f10e";
    font-family: "Font Awesome 6 free"; 
	  font-weight: 900;
    color: var(--yellow);
    font-size: 6rem;
    line-height: 0;
    bottom: -5%;
    right: 0;
}


/* Blockquote subheader */
.blockquote h4 {
    position: relative;
    color: var(--yellow);
    font-size: 1.2rem;
    font-weight: normal;
    line-height: 1;
    margin-top: 1rem;
    z-index: 1;
}

main h2{
  font-weight: 800;
  font-size: 1.2rem;
  position: relative;
}

main h2::after{
  content: '';
  position: absolute;
  width: 1.5rem;
  border-bottom: 3px var(--yellow) solid;
  bottom:0;
  left:0;
}

.about img{
  display: block;
  height: 11rem;
  width: 11rem;
}

.about > div > div:nth-of-type(1){
flex-shrink: 0;
}

.about p{
  text-align: center;
}


.stack > div > div{
  padding: 1rem;
  border-radius: 10%;
}


.stack img{
  height: 4.5rem;
  -webkit-filter: drop-shadow(5px 5px 3px rgb(12, 12, 12));
  filter: drop-shadow(5px 5px 3px rgb(12, 12, 12));
}

.stack_icon{
  /* transform: translateY(0); */
  transform: scale(1) translateY(0);
  transition: all ease-in-out 300ms;
}

.stack_icon.pop{
  transform: scale(1.2) translateY(-.6rem);
  /* transform: translateY(-1rem); */
}

.certifications{
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
}


.cert_list{
  border-left: 3px solid var(--yellow);
  padding: 2rem 1rem;
  position: relative;
}

.cert_list a{
  color: var(--yellow);
  transition: all ease 100ms;
}

.cert_list a:hover{
  color: var(--yellow);
  text-decoration: underline;
font-weight: bold;
}

.cert_list::before{
  content: '\f111';
  font-family: "Font Awesome 6 free"; 
	font-weight: 900;
  font-size: 1rem;
  color: var(--yellow);
  position: absolute;
  left:-10px;
}

.projects{
  overflow: hidden;
}

.projects > div{
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
}

.project_card{
  flex-grow: 1;
  height: 20rem;
  min-width: 5rem;
  width: 20rem;
  position: relative;
  overflow: hidden;
  cursor: pointer;
  box-shadow: 0px 0px 10px rgb(12, 12, 12);
}


.project_card > div{
  position: absolute;
  background-color: rgb(0, 0, 0, .9);
  bottom: 0;
  width: 100%;
  padding: 1rem;
  transform: translateY(0);
  transition: all ease-in-out 500ms;
}

.project_card_image{
position: absolute;
height: 100%;
width: 100%;
object-fit: cover;
transition: all ease-in-out 500ms;
filter: brightness(100%);
}

.project_card a{
  padding: .5rem 1rem;
  background-color: var(--yellow);
  color: black;
  transition: all ease-in-out 200ms;
  font-weight: bold;
}

.project_card a:hover{
transform: translateY(-7px);
}

.project_card:hover > *{
  transform: translateY(0);
}

.project_card:hover > .project_card_image{
  transform: scale(1.2);
  filter: brightness(100%);
}

.contact > div{
  background-color: #18181B;
  box-shadow: 0px 0px 10px rgb(12, 12, 12);
}

.contact form{
  display: flex;
  flex-direction: column;
  gap: 2rem;
}

.contact form > div{
  display: grid;
  grid-template-columns: auto 1fr;
}

label{
padding: 1rem;
font-size: .8rem;
background-color: rgb(20, 20, 20);
display: flex;
justify-content: center;
align-items: center;
}

.contact input, .contact textarea{
  width: 100%;
  background-color: #2b2b31;
  color: white;
  padding: .5rem;
  border: none;
  outline: none;
}

.contact button{
  padding: .5rem 1rem;
  background-color: var(--yellow);
  color: black;
  font-weight: bold;
  font-size: .9rem;
}

footer{
  font-size: .8rem;
}

footer img{
  height: 1.5rem;
}



@media (min-width: 1024px) { 
  html{
    font-size: 17px;
  }

  .bg-image{
    position: relative;
  }

  .page{
    height: 100vh;
    position: relative;
    overflow: hidden;
    max-width: 1600px;
    max-height : 1000px;
    margin: auto
  }

  .content{
    display: grid;
    grid-template-columns: 1fr 4fr;
    overflow: hidden;
    grid-template-rows: 100%;
  }
  
  .main__nav{
    display: flex;
  
  }
  
  #side{
    position: static;
    transform: translateX(0);
    width: 100%;
    height: 100%;
  }
  
  .side_bar_content{
    height: 90%;
  }
  .main{
    width: 94%;
    left: 0;
  }
  
  .main.left{
    left: -13%;
  }
  
  
  #navbar{
    transform: translateX(0);
    height: 100%;
    width: 6%;
    left: 0;
    position: relative;
  }
  
  #navbar.left{
    width: 19%;
    left: -13%;
  }
  .avatar{
    height: 20rem;
  }

  .hero h1{
    font-size: 3rem;
  }

  .main > main{
    overflow: scroll;
    overflow-x: hidden; 
  }

  .main > main::-webkit-scrollbar {
    width: 10px;
  }
  

  .certifications{
    grid-template-columns: 1fr 1fr;
  }

  .project_card > div{
    transform: translateY(150%);
  }
  

  .project_card_image{
  filter: brightness(80%);
  }
  
  .project_card > a{
    transform: translateY(-150%);
  }

  .loading_page{
  height: 100%;
  }

  
 }